import randomSVG from './random.svg';
import audioSVG from './audio.svg';
import 清音 from '../../common/清音';
import 浊音or半浊音 from '../../common/浊音&半浊音';
import { useState } from 'react';
import WChar from '../../common/WChar';
import { isEmpty } from 'lodash';

interface ControllerProps {
    hasQingYin:boolean;
    hasZhuoYin:boolean;
}

const initalWChar = new WChar('xiao', '😊', '🦉', '');

export default function Controller(props: ControllerProps) {
    const [wchar, setWChar] = useState<WChar>(initalWChar);
    const [isShowAnswer, setIsShowAnswer] = useState<boolean>(false);
    const [isPlaying, setIsPlaying] = useState(false)

    const randomWChar = () => {
        let wchars: WChar[] = [];
        if (props.hasQingYin) {
            wchars = [...wchars, ...清音];
        }  
        if (props.hasZhuoYin) {
            wchars = [...wchars, ...浊音or半浊音];
        }     
        const wcharIndex = Math.floor(Math.random() * wchars.length);
        const wchar = wchars[wcharIndex];

        // 取消答案显示
        setIsShowAnswer(false);
        if (isEmpty(wchar)) setWChar(initalWChar);
        else setWChar(wchar);
    };

    const playAudio = () => {
        if (!isEmpty(wchar.audio)) {
            const audio = new Audio(wchar.audio);
            audio.onplay = () => setIsPlaying(true);
            audio.onended = () => setIsPlaying(false);
            audio.play();
        }
    }

    return (
        <div className='w-80 h-28 bg-white rounded-xl flex justify-around items-center shadow-lg relative'>
            {/* <div className='w-20 h-20 bg-yellow-200 rounded-full shadow bg-70% bg-no-repeat bg-center transition-all duration-75 active:shadow-lg' style={{backgroundImage: `url(${previousSVG})`}}></div> */}
            <div className={`w-6 h-6 bg-contain bg-no-repeat bg-center absolute left-3 top-2 ${isPlaying ? 'animate-bounce' : ''}`} style={{backgroundImage: `url(https://gitee.com/archi_chen/cdn/raw/master/audio.svg)`}} onClick={playAudio}/>
            <div className='w-36 h-20 text-red-300 rounded-xl font-roundedFont flex justify-center items-center' onClick={e => setIsShowAnswer(!isShowAnswer)}>
                {
                    isShowAnswer ?
                    <span>
                        <span className='text-6xl mr-3'>{wchar.hiragana}</span>
                        <span className='text-3xl'>{wchar.katakana}</span>
                    </span>
                    :
                    <span className='text-6xl'>{wchar.roman}</span>
                    
                }
            </div>
            <div className='w-20 h-20 bg-yellow-200 rounded-full shadow active:shadow-lg transition duration-75 bg-70% bg-no-repeat bg-center' style={{backgroundImage: `url(https://gitee.com/archi_chen/cdn/raw/master/random.svg)`}} onClick={randomWChar}/>
        </div>
    )
}