class WChar {
    roman: string;
    hiragana: string;
    katakana: string;
    audio: string;
    pic?: string;
  
    constructor(
      roman: string,
      hiragana: string,
      katakana: string,
      audio: string,
      pic?: string
    ) {
      this.roman = roman;
      this.hiragana = hiragana;
      this.katakana = katakana;
      this.audio = audio;
      this.pic = pic;
    }
}

export default WChar;